"use client";
import Link from "next/link";
import { FaFacebookF, FaTwitter, FaInstagram } from "react-icons/fa";
import { usePathname } from "next/navigation";

export default function Footer() {
    const pathname = usePathname();

    const links = [
        { name: "Home", href: "/" },
        { name: "Items", href: "/items" },
        { name: "Add Product", href: "/dashboard/add-product" },
        { name: "Manage Products", href: "/dashboard/manage-products" },
    ];

    const socialIcons = [
        { icon: <FaFacebookF size={20} />, href: "https://facebook.com" },
        { icon: <FaTwitter size={20} />, href: "https://twitter.com" },
        { icon: <FaInstagram size={20} />, href: "https://instagram.com" },
    ];

    return (
        <footer className="bg-green-600 text-white py-10">
            <div className="px-4 md:px-20 mx-auto md:flex md:justify-between md:items-start space-y-8 md:space-y-0">

                {/* ===== Links Section ===== */}
                <div className="space-y-4">
                    <h4 className="font-bold text-lg">Quick Links</h4>
                    <div className="flex flex-col space-y-2">
                        {links.map((link) => (
                            <Link
                                key={link.name}
                                href={link.href}
                                className={`hover:underline transition-colors ${pathname === link.href ? "text-yellow-300 font-semibold" : ""
                                    }`}
                            >
                                {link.name}
                            </Link>
                        ))}
                    </div>
                </div>

                {/* ===== Social Icons Section ===== */}
                <div className="space-y-4">
                    <h4 className="font-bold text-lg">Follow Us</h4>
                    <div className="flex space-x-4">
                        {socialIcons.map((social, index) => (
                            <a
                                key={index}
                                href={social.href}
                                target="_blank"
                                rel="noopener noreferrer"
                                className="hover:text-gray-300 transition-colors"
                            >
                                {social.icon}
                            </a>
                        ))}
                    </div>
                </div>

                {/* ===== Contact Info Section ===== */}
                <div className="space-y-4">
                    <h4 className="font-bold text-lg">Contact</h4>
                    <p>Email: info@naturesplatter.com</p>
                    <p>Phone: +880 1234 567890</p>
                </div>
            </div>

            {/* ===== Scroll To Top Button ===== */}
            <div className="mt-6 text-center">
                <button
                    onClick={() => window.scrollTo({ top: 0, behavior: "smooth" })}
                    className="bg-green-700 hover:bg-green-800 text-white px-4 py-2 rounded transition-all"
                >
                    â†‘ Top
                </button>
            </div>

            {/* ===== Copyright ===== */}
            <div className="mt-10 border-t border-green-500 pt-6 text-center text-sm text-green-200">
                &copy; {new Date().getFullYear()} Natures Platter. All rights reserved.
            </div>
        </footer>
    );
}
